.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


/* Custom CSS */
.backgroundInfo-container {
  width: 40%;
  margin: 20px auto;
  box-shadow: 0px 20px 20px 10px #eee;
  padding: 10px;
  background: #f9f7f7;
}
.label-text{
  float: left;
}
.err {
  color: #d72100;
}
.selectoption {
  padding: 7px;
  width: 100%;
  border-radius: 4px;
  border-color: #d3d3d3;
  height: 40px;
  outline: none;
}
.gender-heading {
  width: 100%;
  text-align: left;
}
.Age-text {
  float: left;
  text-align: left;
  margin-top: 7px;
}
.inner-form {
  margin: 10px auto;
  border: 1px solid #eee;
  padding: 10px;
  border-radius: 4px;
}
.header {
  background: #14ffa8;
  color: #fff;
}
.form-control, .selectoption{
  border-radius: 1.25rem !important;
}
.header-img {
  height: 250px;
  padding: 10px;
  float: left;
  margin: 0 40px;
}
.section {
  box-shadow: 2px 2px 20px #eee;
  margin: 30px;
  margin-right: 25px !important;
  margin-left: 25px !important;
}
.user-info {
  display: block;
  margin: 10px;
}
.searchabr {
  margin: 25px auto;
  width: 85%;
}
.header-heading {
  font-size: 50px;
  font-family: serif;
}

/* media query for phone */
@media screen and (max-width: 480px) {
  .backgroundInfo-container {
    width: 100%;
  }.header-img{
    float: none;
  }
}